<template>
  <div>
    <Navbar class="bg-primary"/>
    <Sidebar/>
    <Banner :data="data"/>
    <Detail :data="data"/>
    <Footer/>
  </div>
</template>

<script>
import Navbar from '@/components/Navbar'
import Sidebar from '@/components/Sidebar'
import Banner from '@/components/DetailCareer/Banner'
import Detail from '@/components/DetailCareer/Detail'
import Footer from '@/components/Footer'
import http from '@/config/http'

export default {
  components: {
    Footer,
    Detail,
    Banner,
    Sidebar,
    Navbar
  },
  props: ['id'],
  data: () => ({
    data: {}
  }),
  methods: {
    getData: async function () {
      await http.get('career/'+this.id)
        .then(res => {
          this.data = res.data.data[0]
        })
    }
  },
  created() {
    this.getData()
  }
}
</script>